@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
*{
  margin: 0px;
  padding: 0px;
  font-family: 'Roboto', sans-serif;
}
.banner{
    height: 550px;
    background-color:rgb(16,16,16);
    background-size: cover;
    background-repeat: no-repeat;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 2rem;
    box-sizing: border-box;

    &::after{
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0%;
        left: 0%;
        background: rgb(16, 16, 16);
        opacity: 0.4;
    }
}
a,h2{
    color: white;
}
a{
    text-decoration: none;
}
.genrebox{
    overflow-x: auto;
    display: flex;
    background-color: rgb(43, 42, 42);
    flex: 0%;
    > a{
        color: black;
        background-color: white;
        padding: 1rem 0;
        text-align: center;
        min-width: 8rem;
        transition: all 0.5s;
        border-radius: 5px;
        margin: 1rem;

        &:hover{
            background-color: rgb(224, 170, 170);
        }
    }
}
h1,p{
    color: white;
    font-size: 3rem;
    width: 40%;
    z-index: 10;
}
p{
    font-size: 1.2rem;
    letter-spacing: 2px;
    word-spacing: 3px;
    font-weight: bolder;
}
>div{
    display: flex;
    button{
        padding: 1rem;
        width: 150px;
        outline: none;
        border: none;
        z-index: 10;
        border-radius: 5px;
        margin: 1rem;
        cursor: pointer;
        font-size: 1.2rem;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.5s;
        &:hover{
            opacity: 0.6;
        }
        &:nth-child(odd){
            background-color: white;
            color: black;
        }
        &:nth-child(even){
            background-color: rgb(57,57,57);
            color: rgb(255,255,255);
        }
        svg{
            font-size: 1.7rem;
            margin: 0 10px;
        }
    }
}


.Head{
    height: 89px;
    background-color: black;
    text-decoration: none;
    >svg{
        color: white;
        font-size: 1.7rem;
        padding-left: 1270px;
        cursor: pointer;

        &:hover{
            color: rgb(242, 120, 120);
        }
    }
    > img{
        height:40px;
        width: 130px;
        padding-left: 10px;
        padding-top: 9px;
    }
    div{
        display: flex;
        justify-content: center;
        text-align: center;
       
        > :link{
            font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
            text-decoration: none;
            margin-left: 60px;
            margin-right: 60px;
            margin-top: 0px;
            font-weight: bolder;
            font-size: 1rem;
            margin-top: 0px;
            color: rgba(78, 229, 179, 0.956);
        }
        >:link:hover{
            color: blue;
        }
    }
}



.Row{

    background-color: black;
    padding: 1rem;

    >h2{
        color: white;
        justify-content: center;
        align-items: center;
        margin: auto;
    }
   > div{
   display: flex;
   overflow-x: auto;
   
   }
}

.card{
    width: 200px;
    height: 150px;
    cursor: pointer;
    margin: 0.46rem;
    transition: transform 0.5s;
    &:hover{
        transform: translate(-8px) scale(1.1);
    }
}
.kanta{
    color: rgba(78, 231, 180, 0.97);
}
.kanta:hover{
    color: blue;
}