.div{
    background: linear-gradient(rgb(22, 22, 189),aqua);
}

.Rowi{

    background:linear-gradient(rgba(2, 23, 54, 0.859),rgba(0, 0, 0, 0.903));
    padding: 1rem;

    >h2{
        color: white;
        justify-content: center;
        align-items: center;
        margin: auto;
    }
   > div{
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
   justify-content: space-around;
   }
}

.cardi{
    width: 200px;
    height: 150px;
    cursor: pointer;
    margin: 0.46rem;
    transition: transform 0.5s;
    &:hover{
        transform: translate(-8px) scale(1.1);
    }
}