@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
.backi{
    background:linear-gradient(rgba(34, 110, 45, 0.866),rgba(88, 24, 147, 0.827));
    height: 98vh;
}
.ul{
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.li{
    color:rgb(3, 4, 42);
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    font-size: 2.4rem;
    font-weight: bold;
    margin: 1.4rem;
    justify-content: center;    
}
.li .btn{
    background:linear-gradient(rgba(195, 9, 208, 0.91),rgba(4, 232, 228, 0.915));
    padding: 0.5rem;
    font-size: 1.5rem;
    margin-left: 2rem;
    border: 2px solid black;
    border-radius: 1rem;
    color:rgb(30, 30, 63);
}
.li .btn:hover{
    color: red;
    cursor: pointer;
    background:linear-gradient(aqua,aquamarine);
}
.btn2{
    background-color: rgb(0, 234, 255);
    box-shadow: 0rem 0rem 1.6rem rgb(0, 255, 238);
    color: whitesmoke;
    text-shadow: 0 0 1rem aqua;
    padding: 0.6rem;
    font-size: 1.5rem;
    font-weight: bolder;
    margin-left: 2rem;
    border: 2px solid aqua;
    border-radius: 1.1rem;
    color:rgb(30, 30, 63);
}
.btn2:hover{
  color: red;
}
.name{
    padding: 9px;
    padding-left: 12px;
    margin-left: 3.5rem;
    width: 28%;
    font-size: 1.3rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border: 2px solid grey;
    border-radius: 1.2rem;
}