*{
  margin: 0rem;
  padding: 0rem;
}
.maxi{
    background: url('./P4.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    height: 95vh;
    filter: blur(5px);
    -webkit-filter: blur(4px);
    z-index: -1;
}
.gxi{
  position: absolute;
  top: 45.5%;
  left: 50%;
  transform: translate(-50%,-50%);
  z-index: 2;
  text-align: center;
  width: 100%;
  margin-top: 0rem;
}
#cont{
    border: 1px solid black;
    box-shadow: 15px 15px black;
     background: linear-gradient(rgba(5, 5, 65, 0.827),rgba(0, 0, 255, 0.833));
    width: 45%;
    min-width: 330px;
    height: 30rem;
    min-height: 450px;
    align-items: center;
    display: flex;
    flex-direction: column;
    margin: auto;
    margin-top: 12%;
    justify-content: center;
    padding-top: 8px;
    padding-bottom: 8px;
    padding-left: 4px;
    padding-right: 4px;
   border-radius: 8px;
 }
 
 #email{
   color: black;
   width: 20rem;
   border: 2px solid white;
   border-radius: 4px;
   background:linear-gradient(rgba(169, 168, 212, 0.96),rgba(142, 216, 220, 0.953));
   padding: 7px;
   margin-inline: 0.5rem;
   margin-top: -1rem;
   font-size: large;
   font-weight: bolder;
   font-family: 'Roboto', sans-serif;
 }
 #password{
   color: black;
   width: 20rem;
   border: 2px solid white;
   border-radius: 4px;
   background:linear-gradient(rgba(169, 168, 212, 0.96),rgba(149, 226, 231, 0.945));
   padding: 7px;
   margin-inline: 0.5rem;
   margin-top: 2rem;
   font-size: large;
   font-weight: bolder;
   font-family: 'Roboto', sans-serif;
 }
 #btn{
   color: black;
   width: 21rem;
   height: 2.65rem;
   border: 2px solid black;
   border-radius: 5px;
   background-color:rgb(224, 24, 24);
   padding: 5px;
   margin-inline: 0.5rem;
   margin-top: 1.4rem;
   margin-bottom: 1.4rem;
   font-size: large;
   font-weight: bolder;
   box-shadow:black;
   cursor: pointer;
 }
 .har{
   text-align: center;
   font-size: larger;
  font-family: 'Courier New', Courier, monospace;
   background: linear-gradient(aqua,rgb(16, 16, 107));
   margin-top: 0rem;
   padding: 1rem;
   height: 2rem;
 }
 #btn:hover{
   background-color: rgba(10, 242, 10, 0.974);
   border-radius: 4px;
   color: blueviolet;
 }
 #btn2{
   color: white;
   width: 21rem;
   border: 2px solid black;
   border-radius: 4px;
   background-color: rgba(12, 240, 12, 0.958);
   padding: 7px;
   margin-inline: 0.5rem;
   margin-top: 1.2rem;
   font-size: large;
   font-weight: bolder;
   font-family: 'Roboto', sans-serif;
 }
 #btn2:hover{
   background-color: aqua;
   border-radius: 3px;
   color: blueviolet;
   cursor: pointer;
 }
 #btn2:active{
  background-color: aqua;
  border-radius: 3px;
  color: red;
 }
 #btn3{
   color: white;
   width: 21rem;
   border: 2px solid black;
   border-radius: 4px;
   background-color: rgba(12, 240, 12, 0.958);
   padding: 7px;
   margin-inline: 0.5rem;
   margin-top: 1.7rem;
   font-size: large;
   font-weight: bolder;
   font-family: 'Roboto', sans-serif;
 }
 #btn3:hover{
  background-color: aqua;
  border-radius: 3px;
  color: blueviolet;
  cursor: pointer;
}
#btn2:active{
  background-color: aqua;
  border-radius: 3px;
  color: red;
 }
 .navi{
   border: 3px solid black;
   border-radius: 2px;
   margin-inline: 0.5rem;
   margin-top: 2rem;
   padding: 1.3rem;
   width: 18rem;
   height: 1.2rem;
   background: white;
   text-align: center;
   font-weight: bolder;
 }
 .navi a{
     color: blue;
     text-decoration:underline;
 }
 .navi link:hover{
  color: aqua;
  cursor: pointer;
 }
 .navi link:active{
    color: red;
    cursor: pointer;
 }

 @media(max-width:1090px){
     html{
      font-size: 81%;
     }
 }
 @media(max-width:836px){
  html{
   font-size: 72%;
  }
}
@media(max-width:750px){
  html{
   font-size: 68%;
  }
}